body,
html {
  font: 16px/1.5em sans-serif;
  background: #ffc600; }

.titulo-principal {
  color: #fff; }

.todo-list {
  width: 400px;
  margin: 50px auto;
  background: white;
  border-radius: 4px;
  box-shadow: 7px 7px 10px rgba(0, 0, 0, 0.25); }
  .todo-list .list-element {
    padding: 0;
    margin: 0; }
    .todo-list .list-element .list-item {
      list-style: none;
      color: #666;
      border-bottom: 1px solid #F1F1F1; }
      .todo-list .list-element .list-item:last-child {
        border-bottom: 0; }
      .todo-list .list-element .list-item .checkbox {
        display: flex;
        align-items: center; }
        .todo-list .list-element .list-item .checkbox input[type="checkbox"] {
          margin: 0 20px; }
          .todo-list .list-element .list-item .checkbox input[type="checkbox"]:checked + .description {
            background: #F9F9F9;
            text-decoration: line-through; }
        .todo-list .list-element .list-item .checkbox .description {
          margin: 0;
          padding: 1em;
          border-left: 1px solid #D1E2FF; }
  .todo-list .todo-form {
    display: flex;
    padding: 1.5em 1em;
    align-items: center;
    justify-content: space-around; }
    .todo-list .todo-form input[type=text] {
      width: 65%;
      padding: 0.5em 0.5em;
      color: #666;
      outline: none;
      border: 1px solid #ccc;
      border-radius: 4px;
      font-size: 1.0em; }
    .todo-list .todo-form input[type=submit] {
      border: 1px solid #ccc;
      border-radius: 4px;
      background-color: #fff;
      padding: 0.68em 1em;
      height: auto;
      transition: all 0.5s;
      cursor: pointer; }
    .todo-list .todo-form input[type=submit]:hover {
      background-color: #dca84e;
      color: #fff; }
